.swiper-slide {
  width: 80%;
}

.header {
  position: fixed;
  z-index: 20;
  width: 100%;
  background: transparent;
}
.header .container {
  padding-top: 42px;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
}
.header__left {
  justify-content: space-between;
}
.header__right {
  justify-content: space-between;
  width: 26%;
}
.header__information {
  display: flex;
}
.header__logo {
  display: flex;
}
.header__logo-img {
  width: 55px;
  height: 55px;
}
.header__logo-icon {
  width: 100%;
  height: 100%;
  fill: var(--color-theme);
  stroke: var(--color-theme);
}
.header__logo-desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 12px;
  color: var(--color-main);
  pointer-events: none;
}
.header__logo-desc span {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.25em;
}
.header__search-btn {
  display: flex;
  padding-top: 5px;
  cursor: pointer;
}
.header__search-btn:hover .header__search-icon {
  fill: var(--color-theme);
}
.header__search-text {
  margin-left: 15px;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}
.header__search-logo {
  display: block;
  width: 15px;
  height: 15px;
}
.header__search-icon {
  width: 100%;
  height: 100%;
  fill: var(--color-main);
  transition: fill 0.3s ease;
}
.header__phone {
  margin-left: 145px;
}
.header__mail {
  margin-left: 75px;
}
.header__left, .header__right {
  display: flex;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: var(--color-main);
}
.header__phone, .header__mail {
  font-weight: 600;
  font-size: 16px;
  line-height: 35px;
}
.header__menu {
  display: flex;
  padding: 8px 0 0;
  cursor: pointer;
}
.header__menu-icon {
  position: relative;
  margin-right: 15px;
  max-height: 18px;
}
.header__menu-line {
  position: absolute;
  display: inline-block;
  width: 28px;
  height: 2px;
  background-color: var(--color-main);
  transition: background-color 0.3s ease;
}
.header__menu-desc {
  display: inline-block;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}
.header__menu:hover .small-line:first-child::before, .header__menu:hover .small-line:last-child::before {
  width: 0;
}
.header .big-line {
  width: 28px;
  transform: translate(0%, 300%);
}
.header .small-line {
  position: relative;
  display: block;
  width: 28px;
}
.header .small-line:first-child {
  top: 0;
  transform: translate(0%, 100%);
}
.header .small-line:first-child::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 50%;
  height: 100%;
  background-color: var(--color-main-bg);
  transition: width 0.3s ease;
}
.header .small-line:last-child {
  bottom: 0;
  transform: translate(0%, 600%);
}
.header .small-line:last-child::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  background-color: var(--color-main-bg);
  transition: width 0.3s ease;
}

@media screen and (max-width: 1820px) {
  .header .container {
    padding: 26px 40px;
  }
  .header__logo-img {
    width: 45px;
    height: 45px;
  }
  .header__logo-desc span {
    font-size: 16px;
    line-height: 18px;
  }

  .header__phone,
.header__mail {
    font-size: 13px;
    line-height: 30px;
  }

  .header__phone {
    margin-left: 120px;
  }

  .header__right {
    width: 387px;
  }
}
@media screen and (max-width: 1024px) {
  .header {
    z-index: 3;
    background: var(--color-main-bg);
  }
  .header__phone {
    margin-left: 30px;
  }
  .header__mail {
    margin-left: 30px;
  }
  .header__right {
    max-width: 140px;
  }
  .header__search-text {
    display: none;
  }
}
@media screen and (max-width: 767px) {
  .header {
    background-color: var(--color-main-bg);
  }
  .header .container {
    padding: 0 10px;
  }
  .header-wrapper {
    position: relative;
    padding: 10px 0;
  }
  .header__left {
    flex-wrap: wrap;
    width: 100%;
  }
  .header__logo {
    order: 1;
  }
  .header__logo-img {
    width: 39px;
    height: 39px;
  }
  .header__logo-desc span {
    font-size: 12px;
  }
  .header__information {
    flex-wrap: wrap;
    justify-content: space-between;
    order: 3;
    margin-top: 15px;
    width: 100%;
  }
  .header__phone {
    margin-left: 0;
    font-size: 13px;
    line-height: 16px;
  }
  .header__mail {
    margin-left: 0;
    font-size: 12px;
    line-height: 14px;
  }
  .header__right {
    position: absolute;
    right: 0;
    top: 0;
    order: 2;
  }
  .header__search-btn {
    display: none;
  }
  .header__menu-desc {
    display: none;
  }
}
.main {
  position: relative;
  overflow: hidden;
  color: var(--color-main);
}
.main-circle {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  fill: none;
}
.main-circle:first-child {
  stroke-dasharray: 17%;
  transform: translate(40%, -70%);
}
.main-circle:nth-child(2) {
  stroke-dasharray: 30%;
  transform: translate(15%, -70%);
}
.main .nav {
  left: 0;
  top: -5px;
  width: 200px;
  height: calc(100% - 130px);
}
.main .nav-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.main .nav-item {
  position: relative;
  margin: 12px 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 23px;
  letter-spacing: -0.4px;
  text-transform: uppercase;
  transform: translate(0, 0);
  transition: opacity 0.2s linear, transform 0.2s linear;
  cursor: pointer;
}
.main .nav-item::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 75px;
  height: 2px;
  background: var(--color-theme);
  opacity: 0;
  transform: translate(-250%, -50%);
  transition: transform 0.4s ease, opacity 0.1s ease;
}
.main .nav-item:hover {
  opacity: 0.75;
}
.main .nav-item.active-link {
  color: var(--color-theme);
  transform: translate(20px, 0);
}
.main .nav-item.active-link::before {
  opacity: 1;
  transform: translate(-125%, -50%);
}
.main .progress-mobile {
  display: none;
}
.main-content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 90px;
  width: 100%;
  height: calc(var(--vh, 1vh) * 100);
  height: 100vh;
  min-width: 0;
}
.main-slider {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100% - 130px);
  min-width: 0;
}
.main-slider__wrapper {
  width: calc(100% - 210px);
  height: 92%;
  min-width: 0;
}
.main-slider__item {
  position: relative;
  display: flex;
  width: 80%;
}
.main-slider__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-left: 95px;
  width: 100%;
  min-width: 0;
}
.main-slider__left-content {
  display: block;
  padding: 0 20px 60px;
  min-height: 380px;
}
.main-slider__left.is-active {
  display: none;
  animation: close-slider-modal 0.3s linear;
  animation-fill-mode: forwards;
}
.main-slider__right {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: 35%;
}
.main-title {
  margin: 6px 0 11px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: -2.6px;
  opacity: 0;
  transform: translate(-15%, 0);
  transition: all 0.5s ease-in-out;
  transition-delay: 0.1s;
}
.main-title span {
  display: inline-block;
  width: 100%;
}
.main-desc {
  padding: 20px 7px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  letter-spacing: -0.35px;
  opacity: 0;
  transform: translate(-15%, 0);
  transition: all 0.5s ease-in-out;
  transition-delay: 0.25s;
}
.main-btns {
  display: flex;
  margin: 35px 0 0;
  padding: 15px 0 15px 5px;
  width: 100%;
}
.main-btns .btn {
  margin-right: 15px;
}
.main-btns .btn-full {
  margin-right: 15px;
}
.main-btns .btn-full:hover {
  color: #fff;
}
.main__recall {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
  display: block;
  width: 150px;
  height: 150px;
}
.main__recall-logo {
  position: absolute;
  width: 100%;
  height: 100%;
}
.main__recall-circle {
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 40px;
  width: 75px;
  height: 75px;
  background-color: var(--color-theme);
  transform: translate(50%, 50%);
}
.main__recall-circle::before, .main__recall-circle::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 2px;
  height: 19px;
  background-color: #fff;
  transform: translate(-50%, -50%) rotate(0deg);
}
.main__recall-circle::before {
  transform: translate(-50%, -50%) rotate(90deg);
}
.main__recall-icon {
  position: relative;
  width: 100%;
  max-width: 100%;
  animation: recall-text 10s linear infinite;
  animation-delay: 0.1s;
}
.main__recall:hover {
  opacity: 1;
}
.main__recall:hover .main__recall-icon {
  opacity: 1;
}
.main__recall:hover .main__recall-circle {
  opacity: 0.75;
}

@keyframes recall-text {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-main);
  border-radius: 50px;
  width: 100%;
  height: 47px;
  max-width: 200px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-main);
  background-color: var(--color-main-bg);
  transition: opacity 0.2s ease;
}
.btn:hover {
  color: var(--color-main);
  opacity: 0.75;
}

.btn-full {
  color: var(--color-white);
  background-color: var(--color-main);
}

.theme-color {
  color: var(--color-theme);
}

.arrow {
  position: relative;
  display: inline-block;
  margin-left: 26px;
  border-top: 2px solid var(--color-main);
  border-right: 2px solid var(--color-main);
  width: 10px;
  height: 10px;
}
.arrow::after {
  content: "";
  position: absolute;
  left: 5px;
  top: -4px;
  width: 2px;
  height: 16px;
  background-color: var(--color-main);
  transform: rotate(45deg);
}

.main-footer {
  position: absolute;
  bottom: 40px;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  padding-left: 180px;
  width: 100%;
}

.main-socials__wrapper {
  display: flex;
  width: 100%;
}
.main-socials__link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 7px;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  background-color: var(--color-main);
  transition: opacity 0.3s;
}
.main-socials__link:hover {
  background-color: var(--color-theme);
}
.main-socials__icon {
  display: inline-block;
  margin: 0 auto;
  max-width: 19px;
  max-height: 19px;
  text-align: center;
  fill: #fff;
}

.main-pagination {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  margin-left: 100px;
  max-width: 180px;
}
.main-pagination__num {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
}

.progress {
  position: relative;
  display: inline-block;
